import React, { Component } from 'react'
import { Panel, PanelHeader, Placeholder, Button, SimpleCell, Avatar, ScreenSpinner } from '@vkontakte/vkui'
import Icon56UsersOutline from '@vkontakte/icons/dist/56/users_outline';
import bridge from '@vkontakte/vk-bridge'
import { VKMiniAppAPI } from '@vkontakte/vk-mini-apps-api'
import SnackbarError from '../components/SnackbarError';



export class Friends extends Component {
  state = {
    friends: null,
    snackbar:null
  }

  componentDidMount() {
    this.getFriends()
  }
  
  getFriends = () => {
    this.props.setPopout(<ScreenSpinner />)
    bridge.sendPromise("VKWebAppGetAuthToken", {
      app_id: this.props.state.appId,
      scope: "friends"
    })
    .then(data => {
      if(data.access_token){
        if(data.scope === "friends"){
          bridge.sendPromise("VKWebAppCallAPIMethod", {
            method: "friends.get",
            params: {
              access_token: data.access_token,
              fields: "city,photo_200,country,bdate",
              v: 5.107
            },
            request_id: '3'
          })
          .then(data => {
            if(data.response.items){
              this.setState({
                friends: data.response.items.filter((item) => item.city).filter((item) => 
                  item.city.id !== this.props.state.user.city.id
                )
              })
              this.props.setPopout(null)
            }else{
              this.props.setPopout(null)
            }
          })
          .catch(() => this.props.setPopout(null))
        }else{
          this.props.setPopout(null)
        }
      }else{
        this.props.setPopout(null)
      }
    })
    .catch(() => this.props.setPopout(null))
  }

   isEmpty(obj) {
    for (let key in obj) {
      return false;
    }
    return true;
  }

  getPrice = (city) => {

  }

  date(date) {
    if(date == "" || date == null) {
      return "Не указан";
    }
    let arr = date.split('.');
    switch (arr[1]) {
      case '1':
        return arr[0]+' января';
      break

      case '2':
        return arr[0]+' февраля';
      break

      case '3':
        return arr[0]+' марта';
      break

      case '4':
        return arr[0]+' апреля';
      break
      case '5':
        return arr[0]+' мая';
      break
      case '6':
        return arr[0]+' июня';
      break
      case '7':
        return arr[0]+' июля';
      break
      case '8':
        return arr[0]+' августа';
      break
      case '9':
        return arr[0]+' сентября';
      break

      case '10':
        return arr[0]+' октября';
      break 
        
      case '11':
        return arr[0]+' ноября';
      break
      case '12':
        return arr[0]+' декабря';
      break
    }
  }

  open = (friend) => {
    console.log(friend)
    this.props.setPopout(<ScreenSpinner />)
    fetch("https://cors-anywhere.herokuapp.com/https://api.travelpayouts.com/data/ru/cities.json")
    .then(response => response.json())
    .then(cities => {
      let myCode = cities.filter((item) => item.name === this.props.state.user.city.title)[0].code
      const friendCode = cities.filter((item) => item.name === friend.city.title)[0].code
      fetch(`https://cors-anywhere.herokuapp.com/https://api.travelpayouts.com/v1/prices/calendar?&origin=${myCode}&destination=${friendCode}&calendar_type=departure_date&token=d378bb3f3b879e6fc87899314ba5ce5d`)
      .then(response => response.json())
      .then(data => {
        console.log(data);
        let indexPrice = Object.keys(data.data)[0]
        if(Object.keys(data.data).length > 3){
          indexPrice = Object.keys(data.data)[3]
        }
        this.props.onChangeGroups('friend', {...friend, price: data.data[indexPrice].price}); 
        this.props.openModal('bullet_to_friend')
        this.props.setPopout(null)
      }).catch(err => {
        this.props.setPopout(null)
        this.setState({snackbar:<SnackbarError text={"Не нашли цену билета к этому другу :("} close={() => this.setState({snackbar:null})}/>})
      })
    }).catch(err => {
      this.props.setPopout(null)
      this.setState({snackbar:<SnackbarError text={"Не нашли город друга :("} close={() => this.setState({snackbar:null})}/>})
    })

  }

  render() {
    const {
      id
    } = this.props
    const {
      friends
    } = this.state
    return (
      <Panel id={id}>
        <PanelHeader>Друзья</PanelHeader>
        {
          friends ? 
          friends.map((friend, i) => 
          <SimpleCell key={i} before={<Avatar src={friend.photo_200}/>} multiline
          description={friend.city == null || friend.country == null ? '??':friend.country.title+". "+friend.city.title+". День рождения: "+this.date(friend.bdate)}
          onClick={() => this.open(friend)}>
          {friend.first_name+" "+friend.last_name}
          </SimpleCell>
          )
          :
          <Placeholder
            icon={<Icon56UsersOutline />}
            header="Друзья"
            action={<Button onClick={this.getFriends} size="l">Показать список</Button>}
          >
            Чтобы получить список к друзьям, вам нужно предоставить доступ приложению.
          </Placeholder>
        }
        {this.state.snackbar}
      </Panel>
    )
  }
}

export default Friends
